@import 'variables';

html,
body {
	width: 100%;
	height: 100%;
	font-family: 'Open Sans','Helvetica Neue',Arial,sans-serif;
	font-weight: 400;
}

p {
	font-size: 16px;
	line-height: 1.5;
	margin-bottom: 25px;
}

section {
	display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
	text-align: center;
}

.page-footer {
	padding-top: 20px;
	text-align: center;
}

.full-height {
	min-height: 100%;
}

.auto-height {
	padding: 80px 0;
}

.learn-more-icon {
	width: 24px;
	float: right;
	margin-left: 10px;
	fill: $color-primary-contrast;
}

#intro .mantra {
	margin-top: 20px;
	font-size: 20px;
	color: black;
	text-shadow: 1px 1px 1px rgba(255, 255, 255, 0.2);
}

header {
	position: relative;
	width: 100%;
	min-height: auto;
	text-align: center;
	background-image: url('../images/header-sm.jpg');
	background-position: center;
	-webkit-background-size: cover;
	-moz-background-size: cover;
	background-size: cover;
	-o-background-size: cover;
}

header .header-content {
	position: relative;
	width: 100%;
	padding-top: 100px;
	padding-bottom: 70px;
	text-align: center;
}

.intro-logo {
	width: 100px;
	height: auto;
}

header .header-bg {
	width: 600px;
	height: 600px;
	background: rgba($color: #fff, $alpha: 0.7);
	border-radius: 50%;
}

header .header-content h1 {
	margin-top: 0;
	margin-bottom: 20px;
	font-size: 50px;
	font-weight: 300;
	text-shadow: 1px 1px 2px rgba(255,255,255,0.4);
}

header .header-content p {
	font-size: 16px;
	font-weight: 300;
	color: rgba(0,0,0,.7);
}

.section-heading {
	font-size: 30px;
	font-weight: 300;
	margin-bottom: 15px;

	&:first-child {
		margin-top: 0;
	}
}

.dark {
	color: #fff;
	background-color: #303030;
}

.text-right {
	text-align: right;
}

.content {
	max-width: 1000px;
	padding: 0 50px;
	color: #656565;
}

.navbar {
	position: fixed;
	display: flex;
	box-sizing: border-box;
	justify-content: center;
	width: 100%;
	height: $nav-height;
	overflow: hidden;
	margin-bottom: 1em;
	border-bottom: 1px solid rgba(255,255,255,.5);
	-webkit-font-smoothing: antialiased;
	z-index: 100;
	-webkit-transition: all .35s;
	-moz-transition: all .35s;
	transition: all .35s;

	.brand {
		display: flex;
		align-items: center;
		transition: all .35s;
	}

	.brand > img {
		opacity: 0;
		width: 32px;
		height: auto;
		margin-right: 15px;
		-webkit-transition: all .35s;
		-moz-transition: all .35s;
		transition: all .35s;
	}

	.brand,
	.brand a {
		text-decoration: none;
		color: black;
	}

	&--hover {
		background-color: #fff;
		border-bottom: none;
		box-shadow: 0px 3px 10px 0px rgba(0,0,0,0.2);

		.brand > img {
			opacity: 1;
			margin-right: 8px;
		}

		.brand,
		.brand a {
			letter-spacing: normal;
		}
	}

	&-inner {
		display: flex;
		width: 100%;
		max-width: 1200px;
		padding: 0 50px;
	}
}

.navbar-inner .pure-menu,
#navlist {
	height: 100%;
	display: flex;
	align-items: center;
}

.pure-menu-horizontal .pure-menu-item {
	display: inline-flex;
	align-items: center;
}

.pure-menu-item a.pure-menu-link {
	color: rgba($color: #000000, $alpha: 0.7);
}

.pure-menu-item.active a.pure-menu-link,
a.pure-menu-link:hover {
	color: rgba($color: #000000, $alpha: 1);
}

#navlist li.active::after {
	display: block;
	position: absolute;
	content: " ";
	bottom: 0px;
	border-bottom: 2px solid $color-primary;
	width: 100%;
	transition: all 0.2s;
}

.parallax-window {
	display: flex;
	flex-direction: column;
	height: 300px;
	width: 1200px;
    justify-content: center;
    align-items: center;
}

.nl-logo {
	height: 60px;
	width: auto;
}

::-moz-selection{
	text-shadow: none;
	color: #fff;
	background: #222;
}

::selection{
	text-shadow: none;
	color: #fff;
	background: #222;
}
img::selection{
	color: #fff;
	background: 0 0;
}
img::-moz-selection{
	color: #fff;
	background: 0 0;
}

.text-light{
	color: rgba(255,255,255,.7);
}

.text-primary{
	color: $color-primary;
}

.bg-primary{
	background-color: $color-primary;
}

.spacer {
	flex: 1;
}


@media (min-width: 768px) {
	#intro .mantra {
		font-size: 30px;
	}

	.section-heading {
		font-size: 60px;
	}

	header {
		min-height: 100%;
		background-attachment: fixed;
		background-image: url('../images/header-lg.jpg');
	}

	header .header-content {
		position: absolute;
		top: 50%;
		padding: 0;
		-webkit-transform: translateY(-50%);
		-ms-transform: translateY(-50%);
		transform: translateY(-50%);
	}

	.intro-logo {
		width: 200px;
	}

	header .header-content .header-bg {
		display: flex;
		justify-content: center;
		align-content: center;
		flex-direction: column;
		margin-right: auto;
		margin-left: auto;
		max-width: 1000px;
	}

	header .header-content h1 {
		font-size: 100px;
	}

	header .header-content p {
		margin-right: auto;
		margin-left: auto;
		max-width: 80%;
		font-size: 18px;
	}
}


@media (max-width: 768px) {
	.navbar {
		display: none;
	}

	.content {
		padding: 0 20px;
	}
}
